import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { HelpdeskComponent } from './helpdesk.component';
import { HelpdeskSetupComponent } from './components/helpdesk-setup/helpdesk-setup.component';
import { HelpdeskUpdateTicketComponent } from './components/helpdesk-update-ticket/helpdesk-update-ticket.component';
import { HelpdeskAllTicketsComponent } from '../../shared/components/helpdesk-all-tickets/helpdesk-all-tickets.component';
import { HelpdeskUnassignedComponent } from './components/helpdesk-unassigned/helpdesk-unassigned.component';
import { HelpdeskReportsComponent } from './components/helpdesk-reports/helpdesk-reports.component';
import { AddTicketComponent } from '../../shared/components/add-ticket/add-ticket.component';
import { HelpdeskEditStaffComponent } from './components/helpdesk-setup/helpdesk-edit-staff/helpdesk-edit-staff.component';


const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: HelpdeskSetupComponent},
	{ path: 'edit-staff/:id', component: HelpdeskEditStaffComponent},
	{ path: 'add-ticket', component: HelpdeskUpdateTicketComponent},
	{ path: 'edit-ticket/:id', component: AddTicketComponent },
	{ path: 'all-tickets', component: HelpdeskAllTicketsComponent},
	{ path: 'unassigned', component: HelpdeskUnassignedComponent},
	{ path: 'reports', component: HelpdeskReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HelpdeskRoutingModule { }
