import { Component, OnInit } from '@angular/core';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-helpdesk-reports',
  templateUrl: './helpdesk-reports.component.html',
  styleUrls: ['./helpdesk-reports.component.scss']
})
export class HelpdeskReportsComponent implements OnInit {

  reportDataList:any;

  isDataLoaded:boolean = false;

  constructor(
    private lookupService: LookupService,
    private cookieService:CookieService
  ) { }



  ngOnInit() {

    let details = {
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      LookupTypeId: 87, //lookuptypeid for report
      MenuName: 'HelpdeskTracker'
    }

    this.lookupService.getLookupValuesByApartmentIdLookupTypeIdMenuName(details).subscribe((res:any) => {

      this.reportDataList = res;

      this.isDataLoaded = true;

    })


  }

}
