import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-helpdesk-update-ticket',
  templateUrl: './helpdesk-update-ticket.component.html',
  styleUrls: ['./helpdesk-update-ticket.component.scss']
})
export class HelpdeskUpdateTicketComponent implements OnInit {
  
  ticketArray:any;
  ticketCount:number = 1;
  isEditTicket:boolean = false;


  constructor(
  	private router: Router,
    private route: ActivatedRoute
  ) { }


  
  addTicket() {
    this.ticketCount++;
    this.ticketArray =  Array(this.ticketCount).fill(0).map((x,i)=>i);
  }

  getTicketArray(event){
    this.ticketArray = event;
    this.ticketCount = this.ticketArray.length;
  }

  isMobileView() {
    return window.innerWidth <= 576;
  }

  ngOnInit() {


    if(this.route.params['value'].id != undefined){
      this.isEditTicket = true;
    }

    this.ticketArray =  Array(this.ticketCount).fill(0).map((x,i)=>i);

  	

  }

}
